import React, {useState, useCallback} from 'react';
import {useDropzone} from 'react-dropzone'
import './App.css';
import "bootstrap/dist/css/bootstrap.css";
import {Board} from "./Board";


async function readFile(file) {
    return await new Promise((resolve => {
        const reader = new FileReader();
        reader.onloadend = () => {
            resolve({[file.name]: reader.result});
        };
        reader.readAsBinaryString(file);
    }))
}

async function readFiles(files) {
    let filesResults = {};
    for (let filesKey in files) {
        filesResults = {...filesResults, ...await readFile(files[filesKey])}
    }
    return Object.entries(filesResults).sort().map(([filename, file]) => file);
}

function MyDropzone({onSetResults}) {
    const onDrop = useCallback(acceptedFiles => {
        readFiles(acceptedFiles).then(files => {
            onSetResults(files.map(file => JSON.parse(file)));
        });
    }, []);
    const {getRootProps, getInputProps, isDragActive} = useDropzone({onDrop});

    return (
        <div style={{height: "200px"}} className="d-flex justify-content-center mt-5 mb-auto drag-area w-50 bg-secondary" {...getRootProps()}>
            <input {...getInputProps()} />
            {
                isDragActive ?
                    <h2 className="d-flex justify-content-center align-self-center">Drop the files here ...</h2> :
                    <h2 className="d-flex justify-content-center align-self-center">Drag 'n' drop outputs of bot here, or click to select files</h2>
            }
        </div>
    )
}

function App() {
    const [results, setResults] = useState([]);

    return (
        <div className="App h-100 d-flex align-items-center justify-content-center flex-column overflow-auto">
            <h1 className="text-white my-5">Welcome to the pywar</h1>
            {results.length <= 0 ? <MyDropzone onSetResults={setResults}/> : <div className="w-100">
                    <Board className="" results={results}/>
                </div>}
            <div className="d-flex align-items-center flex-column mt-auto pt-5">
                <h6 className="font-weight-bold text-white">&copy; Created by Eli Dayan, Tal I Roni, Ori Friedlender, Eden Salmon</h6>
                <a href="https://www.freepik.com/free-photos-vectors/background">Background photo created by ijeab - www.freepik.com</a>
            </div>
        </div>
    );
}

export default App;
