export const Cell = (props) => {
    const {cellInfo} = props;
    const countryToFlag = {
        absurdistan: absurdistan,
        berzerkistan: berzerkistan,
        derkaderstan: derkaderstan,
        botland: botland,
        cobrastan: cobrastan,
        UK: ukFlag,
        USA: usaFlag
    };

    return <td className="cell" style={{
        backgroundImage: `url(${countryToFlag[cellInfo.country]})`,
        backgroundColor: typeof countryToFlag[cellInfo.country] === "undefined" && "gray",
        backgroundSize: "contain",
        borderStyle: "solid",
        borderColor: "gray"
    }}>
        <div className="overflow-hidden" style={{width: "80px", height: "80px"}}>
            {cellInfo.pieces.map(piece => {
                return <img key={piece.id} alt="piece" style={{width: "50px", height: "50px"}}
                            src={COUNTRIES_AND_TYPES_TO_ICONS[piece.country + "_" + piece.type]}/>
            })}
            <div className="text-warning"><img alt="gold" src={gold} width="10"/>{cellInfo.money}</div>
        </div>
    </td>
};
